div.ex {
  margin: 40px 0 10px 25px;
}

div.ex img {
  max-width: 100px;
  max-height: 100px;
  border-radius: 50%;
  padding: 8px 0 0 0;
}

div.ex p {
  max-width: 80%;
  display: inline-block;
  padding: 10px 0 0 20px;
  font-size: 22px;
  line-height: 28px;
  font-family: "Roboto", sans-serif;
  color: #fafafc;
}

div.ex .re {
  padding: 2.5% 0 5px 12%;
}

div.ex .re .span {
  display: block;
  position: relative;
  width: 50px;
  height: 50px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  background-color: white;
  z-index: 2;
}

div.ex .re .span:before,
div.ex .re .span:after {
  position: absolute;
  width: 50px;
  height: 50px;
  content: "";
  border-radius: 50%;
  background-color: white;
  z-index: 2;
}

div.ex .re .span:before {
  left: -25px;
  z-index: 2;
}

div.ex .re .span:after {
  top: -25px;
  z-index: 2;
}

div.ex .re .heart {
  display: block;
  position: relative;
  width: 50px;
  height: 50px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  background-color: #ed0c35;
  z-index: 4;
}

div.ex .re .heart:before,
div.ex .re .heart:after {
  position: absolute;
  width: 50px;
  height: 50px;
  content: "";
  border-radius: 50%;
  background-color: #ed0c35;
  z-index: 4;
}

div.ex .re .heart:before {
  left: -25px;
  z-index: 4;
}

div.ex .re .heart:after {
  top: -25px;
  z-index: 4;
}
